<template>
<div>
    <div class="panel panel-info">
        <div class="panel-body">
            <div>作物信息管理</div>
            <div class="buttonbox" v-show="!inforadmin">
                <button type="button" class="btn btn-info" @click="inforAddClick(true)" v-if="$api.opreate.hasLoginComponents(this,'sys:cropIM:add')">新增</button>
                <button type="button" class="btn btn-warning" @click="updateData" v-if="$api.opreate.hasLoginComponents(this,'sys:cropIM:modify')">修改</button>
                <button type="button" class="btn btn-danger" @click="deleteData" v-if="$api.opreate.hasLoginComponents(this,'sys:cropIM:delete')">删除</button>
                <div class="pull-right col-sm-3 search-input" v-if="$api.opreate.hasLoginComponents(this,'sys:cropIM:search')">
                    <div class="col-sm-12">
                        <div class="col-sm-8 search-input ">
                            <input type="text" class="form-control" placeholder="搜索用户">
                        </div>
                        <input type="button" value="搜索" class="btn btn-info col-sm-3" @click="searchBtnClick">
                    </div>
                </div>
            </div>
            <tables v-show="!inforadmin" :tableSelectData="tableSelectData"></tables>
            <div v-show="inforadmin">
                <form class="form form-horizontal col-sm-8" ref="refForm">
                    <!-- 作物类型 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">作物类型</label>
                        <div class="col-sm-6">
                            <select type="text" name="url" class="form-control" v-model="addData.crop_varieties_id"><option :value="item.cropVarietiesId" v-for="(item,index) of crop_type" :key="index" v-text="item.cropVarietiesName"></option> </select>
                        </div>
                    </div>
                    <!-- 土壤类型 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">土壤类型</label>
                        <div class="col-sm-6">
                            <select type="text" name="url" class="form-control" v-model="addData.soil_type_id"><option :value="item.soilTypeId" v-for="(item,index) of soil_type" :key="index" v-text="item.soilTypeName"></option> </select>
                        </div>
                    </div>
                    <!-- 作物名称 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">作物名称</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('crop_name')&&addData.crop_name!='','has-error':errors.has('crop_name')}">
                            <input type="text" class="form-control" name="crop_name" :placeholder="'请输入作物名称,最多为20位'" v-model="addData.crop_name" :data-vv-as="'作物名称'" v-validate="$api.opreate.validateAdd('name',20,stateid,opreate,'Sensor')">
                        </div>
                        <div class="text-danger">
                            <span v-show="errors.has('crop_name')">{{errors.first("crop_name")}}</span>
                        </div>
                    </div>
                    <!-- 株距 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">株距</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('crop_row_spacing')&&addData.crop_row_spacing!='','has-error':errors.has('crop_row_spacing')}">
                            <input type="text" class="form-control" name="crop_row_spacing" :placeholder="'请输入株距,最多为3位'" v-model="addData.crop_row_spacing" :data-vv-as="'株距'" v-validate="'required'">
                        </div>
                        <div class="text-danger">
                            <span v-show="errors.has('crop_row_spacing')">{{errors.first("crop_row_spacing")}}</span>
                        </div>
                    </div>
                    <!-- 密度 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">密度</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('crop_density')&&addData.crop_density!='','has-error':errors.has('crop_density')}">
                            <input type="text" class="form-control" name="crop_density" :placeholder="'请输入密度,最多为3位'" v-model="addData.crop_density" :data-vv-as="'密度'" v-validate="'required'">
                        </div>
                        <div class="text-danger">
                            <span v-show="errors.has('crop_density')">{{errors.first("crop_density")}}</span>
                        </div>
                    </div>
                    <!-- 备注 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">备注</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('remark')&&addData.remark!='','has-error':errors.has('remark')}">
                            <textarea rows="6" class="form-control" name="remark" :placeholder="'请输入备注'" v-model="addData.remark" :data-vv-as="'备注'" v-validate="'required'"></textarea>
                        </div>
                        <div class="text-danger">
                            <span v-show="errors.has('remark')">{{errors.first("remark")}}</span>
                        </div>
                    </div>
                    <div class="form-group col-sm-12">
                        <button type="button" class=" col-sm-2 col-sm-offset-2 btn btn-info" @click="addDatas">提交</button>
                        <button type="button" class=" col-sm-2 col-sm-offset-2  btn btn-warning" @click="inforAddClick(false)">返回</button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</div>
</template>
植物信息管理
<script>
import tables from "@/components/table.vue";
export default {
    data: function () {
        return {
            inforadmin: false,
            opreate: "add",
            tableSelectData: {
                selectTableurl: "agriculture/selectCropTypeInfo.action",
                pageloginData: {
                    id: this.stateid
                },
                filterName: []
            },
            addData: {
                crop_varieties_id:"",
                soil_type_id:"",
                crop_name:"",
                crop_row_spacing:"",
                crop_density:"",
                remark:""
            },
            crop_type:[],
            soil_type:[]
        }
    },
    props: {
        stateid: String,
        statetitle: String
    },
    computed: {
        checkData() {
            return this.$store.getters.GETUSERTABLEPAGECHECKDATA;
        }
    },
    components: {
        tables,
    },
    methods: {
        //添加按钮按下
        inforAddClick(bool) {
            var vm=this;
            var opreate=this.opreate;
            if(bool){
                 this.publicAddAndUpdate();
            }
            if(opreate=='update'){
                this.addData = this.$api.opreate.ObjectCopy(this.checkData[0]);
            }
            async function Empty(){
                   for (var key in vm.addData) {
                    vm.addData[key] = "";
                    }
                    return true;
            }
          Empty().then(()=>{
            if(vm.$validator.errors.items.length>0){
                vm.$validator.errors.clear()
            }
            vm.opreate = "add";
             vm.inforadmin = bool;
          })
        },
        //添加修改公共函数
        publicAddAndUpdate(){
            this.logincrop_type();
            this.loginsoil_type();
        },
        // 修改按钮按下
        updateData() {
            var vm = this;
            if (vm.$api.opreate.TESTcheck(vm, "修改")) {
                this.publicAddAndUpdate();
                this.inforadmin = true;
                this.opreate = "update";
                this.addData =this.$api.opreate.ObjectCopy(this.checkData[0]);
            }
        },
        //添加函数
        addDatas() {
            var vm = this;
            var stateId = vm.stateid;
            var data = vm.addData;
            var datas = {
                data: data,
                states: {
                    id: stateId
                }
            };
            // 修改操作
           
            
            async function addDataFun(){
             var bool;
            if (vm.opreate == "update") {
               bool = vm.$api.http.opreateHttp(vm, "agriculture/updateCropTypeInfo.action", datas, "修改", "");
            } else if (vm.opreate == "add") {
                //添加操作
                 bool = vm.$api.http.opreateHttp(vm, "agriculture/addCropTypeInfo.action", datas, "添加", "");
            }
            return bool;
            }
            addDataFun().then(function (success) {
                vm.$validator.errors.clear();
                if (success) {
                    setTimeout(function () {
                        vm.inforAddClick(false);
                    }, 800);
                }
            })
        },
        // 删除按钮按下
        deleteData() {
            var vm = this;
            var checkData = vm.$store.getters.GETUSERTABLEPAGECHECKDATA;
            if (vm.$api.opreate.TESTcheck(vm, "删除")) {
                //询问框
                layer.confirm(
                    "是否删除", {
                        btn: ["确认", "取消"] //按钮
                    },
                    function () {
                        var datas = {
                            id: checkData[0].id,
                            states: {
                                id: vm.stateid
                            }
                        };
                        vm.$api.http.opreateHttp(
                            vm,
                            "agriculture/deleteCropTypeInfo.action",
                            datas,
                            "删除"
                        );
                    }
                );
            }
        },
        //搜索按钮按下
        searchBtnClick() {
            var vm = this;
            if (vm.searchData.data != "") {} else {
                vm.$layer.msg("请输入场景类型名后搜索", {
                    icon: 0
                });
            }
        },
        //作物类型获取
        logincrop_type() {
        var vm = this;
        vm.$axios
            .post(vm.HOST + "agriculture/selectCropType.action")
            .then(function(data) {
            if (data.bool) {
                vm.crop_type = data.data;
            }
            })
            .catch(function(error) {
            console.log(error);
            });
        },
        //土壤类型获取
        loginsoil_type() {
        var vm = this;
        vm.$axios
            .post(vm.HOST + "agriculture/selectSoilType.action")
            .then(function(data) { 
            if (data.bool) {
                vm.soil_type = data.data;
            }
            })
            .catch(function(error) {
            console.log(error);
            });
        },
    }
};
</script>

<style>

</style>



